import Stomp from 'stompjs';
let stompClient = null;let username = null;

stompClient.connect = () => {
    stompClient = Stomp.client('/ws/websocket');
    stompClient.heartbeat.outgoing = 5000;
    stompClient.heartbeat.incoming = 0;
    // 
    username = document.cookie;
    stompClient.connect({
        username: username
    }, function (succ) {
        console.log('client connect success:', succ);
        console.log('');

        // 
        // client.subscribe("/topic/accept", onMessage);
        // 
        // client.subscribe("/user/topic/acceptUser", onMessage);
    }, function (error) {
        console.log('client connect error:', error);
        console.log('');
    });
    // destination /app/send/user/sendUser/info
    // apptopicwsapptopic
    // useruser2ws,/user/topic
    stompClient.sendMsg = function (destination, headers, body) {
        stompClient.send(destination, headers, body)
    };
    // 
    stompClient.disConnect = function () {
        stompClient.disconnect();
        console.log('client connect break')
    }
}
export default stompClient;


